*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  background-color: #f1f2f6;

}
.container{
  width: 80vw;
  margin:0 10%;
}
.profile{
  background-color: #fff;
  height: 16rem;
  width: 100%;
  display: flex;
  flex-direction: column;
justify-content: center;
align-items: center;
}
.profilePic{
  width: 20%;
  height: 12rem;
}

.profilePic img{
  width: 100%;
  height: 100%;
}

.profile p{
  font-family: sans-serif;
  font-size: 2rem;
  font-weight: 600;
  color: rgb(64, 63, 63);
  margin-top: 0.5rem;
}

.money{
  margin-top: 0.5rem;
  height: 4.5rem;
  /* background-color: #fff; */
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: linear-gradient(#45dab5, #24ba59);
  color: #fff;
  margin-bottom: 0.5rem;
  position: sticky;
  align-self: flex-start;
  top: 0;
}
.money p{
  /* background-image:linear-gradient(#45dab5, #24ba59) ; */
 /* need to set the number's background to same as parent */
 font-family: sans-serif;
 font-size: 2rem;
 font-weight: 600;
}

.item{
  /* margin-top: 0.5rem; */
  /* width: 33%; */
  background-color: #fff;
  /* height: 18rem; */
padding: 1rem;
}
.itemInfo{
  display: flex;
  flex-direction: column;
  background-color: #fff;
  align-items: center;

}

.image{
  width: 65%;
  height: 200px;
}

.image img{
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.itemInfo>*{
  background-color: #fff;
}
.itemInfo h4{
  font-family: sans-serif;
  font-size: 1.4rem;
  color: rgb(73, 66, 66);
  font-weight: bold;
}

.itemInfo p{
  font-family: sans-serif;
  font-size: 1.15rem;
  color: rgb(15, 204, 97);
  font-weight: 400;
}

.selectItem{
  background-color: #fff;
 display: flex;
 flex-direction: row;
 justify-content: center;
 margin-top: 1rem;

}

.selectItem>*{
  background-color: #fff;

}
.totalQuantity{
  width: 5.5rem;
  height: 2.3rem;
  border-radius: 3px;
  margin:0 0.75rem;
  border: solid 0.85px ;
  text-align: center;
  font-family: sans-serif;
  font-size: 1rem;
  font-weight: 100;
}
.subtractBtn{
  width: 5.5rem;
  height: 2.3rem;
  border-radius: 3px;
  border-width: 0;
  /* background-image: linear-gradient(#f24d4d,#e43727); */
  background-color:  #f1f2f6;
  /* color: #fff; */
  font-size: 1.5rem;
}

.subtractBtn.active{
  background-image: linear-gradient(#f24d4d,#e43727);
  color: #fff;
}

.addBtn{
  width: 5.5rem;
  height: 2.3rem;
  border-radius: 3px;
  border-width: 0;
  background-image:linear-gradient(#45dab5, #24ba59);
  color: #fff;
  font-size: 1.5rem;
}

/* ItemList Design starts */

.itemList{
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 0.5rem;
}

.bill{
  background-color: #fff;
  margin-top: 0.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.bill h3{

  padding-top: 0.5rem;
  font-family: sans-serif;
  font-size: 1.5rem;
  padding-bottom: 0.5rem;
}
.billArea{
  width: 60%;
}
.total{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0.5rem 0;
}
.total h4{
  font-family: sans-serif;
  font-size: 1rem;
}
.total p{
  font-family: sans-serif;
  font-size: 1rem;
  font-weight: 600;
  color: #24ba59;
}
.credit{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 1rem;
  margin-bottom: 0.25rem;
}
.credit p{
  font-family: sans-serif;
  font-size: 0.5rem;
  color: rgb(122, 119, 115);
}
.credit a{
  color: #24ba59;
  text-decoration: none;
}

@media screen and (max-width: 480px) {

.container{
  width: 95vw;
  margin:0 2.5%;
}
.profile p{
  font-size: 1.4rem;
}
.profilePic {
  width: 50%;
}

    .itemList{
      grid-template-columns: repeat(1,1fr);
    }
    
}